#main {
  text-align: center;
}
.container-custom
.container {
}
.sidebar-drive {
  width: 255px;
  height: 100vh;
  float: left;
  position: fixed;
  background-color: $purple_coal;
  .logo-drive {
    background-image: url('../images/logo-sidebar.png');
    background-repeat: no-repeat;
    background-size: 100%;
    height: 170px;
  }
  .logo-footer-sidebar {
    padding-bottom: 35px;
    position: absolute;
    bottom: 0;
    transform: translateX(-50%);
    left: 50%;
  }
  .menu-sidebar {
    // margin-top:123px;
    ul {
      padding:0;
      .active {
        background: $white;
        border-left: 5px solid $yellow;
        padding-left: 10px;
        color: $black; 
      }
      li {
        list-style: none;
        border-top:1px solid #2f323e;
        border-bottom:1px solid #2f323e;
        padding:12px 0 12px 16px;
        font-weight: bold;
        text-align: left;
        &:hover {
          background: $white;
          border-left: 5px solid $yellow;
          padding-left: 10px;
          color: $black;
          transition: 0.5s;
        }
        a {
          text-decoration: none;
          color: $gray;
          i{
            font-size: 20px;
          }
          span {
            margin-left: 18px;
          }
        }
      }
    }
  }
}
.content {
  // float: left;
  margin-left: 255px;
}
