.content-drive-active {
    background: $bg-drive;
    border: 1px solid $bg-drive;
    .nav-tab-drive {
        border: 1px solid red;
        padding:0 10%;
        .tab-drive {
            background: $yellow;
            display: block;
            overflow: hidden;
            text-align: center;
            margin: auto;
            margin-top: 38px;
            padding: 2px;
            .active {
                background: $yellow;
            }
            li {
                font-weight: bold;
                list-style: none;
                float: left;
                font-size: 12px;
                padding: 5px 18px;
                background: $white;
                margin-left: 2px;
                a {
                    text-decoration: none;
                    color: $black;
                }
            }
        }
    }
}

// end tab driver css


.table-content-driver {
        text-align: center;
        .table-drive-child {
            margin-bottom: 20px;
        }
        .table-drive-child-box{
            background: $silver;
            box-shadow: 0 5px 8px 0 rgba(132,141,168,0.29);
        }
        .table-drive-child-bottom {
            .td-driver {
                background: #e8e8e8;
                
            }
        }
        .table-driver {
            margin: auto;
            width: 88%;
            border: none;
            margin-top: 30px;
            border-collapse: collapse;
            display: table;
            .title-table-driver {
                background: $bg-drive !important;
                .td-hos-compliance {
                    color: $black;
                    i {
                        font-size: 20px;
                    }
                }
            }
            .content-table-driver {
                padding-top: 10px;
                margin-bottom: 20px;
                .td-driver-maps {
                    width: 11% !important;
                }
            }
            .tr-driver {
                background: $white;
                color: $lightgray;
                display: table-row;
                .authorized {
                    width: 1.5% !important;
                }
                .td-driver {
                    display: table-cell;
                    border: none;
                    font-size: 11px;
                    margin-top: 10px;
                    padding: 10px;
                    width: 10%;
                    font-weight: bold;
                    .name-task {
                        display: block;
                        overflow: hidden;
                        text-align: left;
                        padding-left: 16px;
                    }
                    img {
                        width: 30px;
                        float: left;
                        height: 30px;
                        border-radius: 50%;
                    }
                    .name-avatar {
                        width: 40px;
                        float: left;
                        height: 40px;
                        border-radius: 50%;
                        color: $black;
                        background: $yellow;
                        padding-top: 10px;
                        font-size: 15px;
                        font-weight: bold;
                    }
                    .name-title-avar{
                        display: block;
                        overflow: hidden;
                        margin: 0;
                        color: $black;
                        font-weight: bold;
                        font-size: 16px;
                    }
                    .text-null-bottom {
                        color: $black;
                        font-weight: bold;
                        font-size: 14px;
                    }
                    span {
                        color: $lightgray;
                        font-size: 14px;
                        font-weight: 600;
                    }
                    .status-duty {
                        text-align: center;
                        .status {
                            border-radius: 3px;
                            color: $white;
                            width: 50%;
                            margin: auto;
                            padding: 3px 0 2px 0;
                            &.onduty{
                                background: $onduty;
                            }
                            &.driving{
                                background: $driving;
                            }
                            &.offduty{
                                background: $offduty;
                            }
                            &.slepper{
                                background: $sleeper;
                            }

                        }
                        .status-duty-icon {
                            color: $eld;
                            font-weight: bold;
                            strong {
                                width: 5px;
                                height: 5px;
                                border-radius: 50%;
                                display: block;
                                float: right;
                                position: relative;
                                top: 8px;
                                right: 36%;
                                &.onduty{
                                    background: $onduty;
                                }
                                &.driving{
                                    background: $driving;
                                }
                                &.offduty{
                                    background: $offduty;
                                }
                                &.slepper{
                                    background: $sleeper;
                                }
                                &.slepper-red{
                                    background: $red;
                                }
                            }
                        }
                    }
                    .fa-check-bottom {
                        color: $driving;
                        font-size: 17px;
                    }
                }
            }
            .location-icon {
                color: $darkgray;
                font-weight: bold;
            }
            .title-time {
                color: $lightgray;
                font-size: 11px;
                font-weight: 300;
            }
            .miles {
                color: $darkgray;
                font-size: 18px;
                font-weight: bold;
            }
            .violation-fa {
                color: $violation;
                font-weight: bold;
                font-size: 19px;
                margin-bottom: 5px;
            }
            .check-violation {
                color: $driving;
            }
            .violation {
                color: $violation;
                font-weight: bold;
            }
        }
    }


    .pagination {
        li {
            .prev {
                color: #d8d8d8;
                font-weight: 600;
            }
            a {
                border:none;
                color: $black;
                margin-left: 2px;
                background: none;
                font-weight: 600;
            }
            .active {
                background: $yellow;
                border-radius: 50%;
            }
        }
    }
