.content-white {
    text-align: center;
    width: 100%;
    background: #f9f9f9;
    padding-bottom: 30px;
    padding-top:30px;
    .content-white-child {
      background:$white;
      width: 70%;
      margin:auto;
      height: 70vh;
    }
  }
    @media (max-width: 768px) { 
    .header {
      position: fixed;
      top: 0;
      width:100%;
      .header-drive {
        .list-menu-icon{
          display: block;
          float: left;
          padding-top:14px;
          font-size: 23px;
          padding-left: 15px;
        }
      }
    }
    .sidebar-drive {
      background-size: 100%;
    }
  }
