.login {
      background-image: url(../images/background.png);
      background-repeat: no-repeat;
      background-size: 100%;
      padding-bottom: 30px;
      height: 100vh;
      .header-logo-driver {
        display: block;
        overflow: hidden;
        a {
          float: left;
          display: block;
          overflow: hidden;
          padding:10px 15px;
        }
        .nav-login {
          padding:0;
          float: right;
          padding-right: 17px;
          padding-top:17px;
          li {
            float: left;
            list-style: none;
            margin-left: 15px;
            a {
              text-decoration: none;
              color: $white;
              font-weight: 700;
              padding-left:27px;
              padding-right: 27px;
              border-radius: 8px;
              &:hover {
                background: $yellow;
                color: $black;
              }
            }
            .active {
              background: $yellow;
              color: $black;
            }
          }
        }
      }
}

.form-login-drive {
  .email {
    color: #888884;
    font-size: 11px;
  }
  h1 {
    font-weight: bold;
    color: $black;
    font-size: 25px;
  }
  text-align: center;
  background: $white;
  padding:40px;
  width:27%;
  margin: 100px auto;
  .form-group-drive {
    position: relative;
    input {
      height: 45px;
    }
    text-align: left;
  }
}
.btn-continue {
  font-size: 19px;
  background: $yellow;
  color: $black;
  font-weight: bold;
  width: 100%;
  border:none;
  padding-top:18px;
  padding-bottom: 18px;
  margin-top:20px;
  box-shadow: 0 5px 8px 0 rgba(132,141,168,0.29);
}
.forget-password {
  color: $black;
  font-weight: bold;
  font-size: 15px;
  text-align: left;
  margin-top:20px;
  text-decoration: none;
  display: block;
}
.sign-in-free {
  color: $eld;
  text-align: left;
  font-size: 15px;
  margin-top: 20px;
  a {
    color: $black;
    text-decoration: none;
    font-weight: bold;
  }
}
.coppy-bottom {
  margin-top: 10px;
  color: $white;
  font-weight: 400;
  font-size: 15px;
}
.icon-email {
  position: absolute;
  width: 45px;
  height: 45px;
  right: 0;
  text-align: center;
  padding-top:14px;
  color: #e2e3e3;
  top: 26px;
}
.has-error {
  color: $red;
  input {
    border:2px solid $red !important;
  }
  i {
      color: $red;
    }
}
  .error-title{
    text-align: left;
    font-weight: 400;
    font-size: 13px;
  }
