.header-drive {
  background-color: $yellow;
  display: block;
  overflow: hidden;
  .list-menu-icon{
    display: none;
  }
  ul {
    float: right;
    padding:0;
    margin:10px 0;
    .icon-bell-details {
      margin-top:10px;
    }
    li {
      float: left;
      list-style: none;
      margin-left: 10px;
      a {
        i {
          color: $black;
          position: relative;
          font-size: 20px;
          span {
            font-size: 10px;
            color: $white;
            background: $red;
            width: 13px;
            border-radius: 50%;
            height: 13px;
            display: block;
            position: absolute;
            padding-top:1px;
            top: -9px;
            right: -7px;
          }
        }
      }
    }
    .img-avatar-icon {
        width: 73px;
        border-radius: 50%;
        img {
          border-radius: 50%;
          height: 32px;
          width: 32px;
        }
        i {
          position: relative;
          top: 3px;
          left: 3px;
          cursor: pointer;
        }
      }
    }
  }
  .title-drive-content {
    text-align: left;
    padding: 20px 0 20px 35px;
    background: $silver;
    box-shadow: 0 5px 8px 0 rgba(132,141,168,0.29);
    display: block;
    overflow: hidden;
    h2 {
      margin:0;
      float: left;
      color: $black;
      font-weight: 600;
    }
    .btn-add-drive{
      background: $yellow;
      float: left;
      text-decoration: none;
      margin-left: 15px;
      padding:7px 10px;
      border-radius: 3px;
      font-weight: bold;
      i {
        color: $black;
      }
      span {
        margin-left: 5px;
        color: $black;
      }
    }
    p{
      float: left;
      width: 28%;
      color: $lightgray;
      margin-top:8px;
      position: absolute;
      left: 50%;
      &::after {
        background-color: $lightgray;
        content: "";
        display: inline-block;
        height: 1px;
        position: relative;
        vertical-align: middle;
        width: 30px;
        margin-left: 5px;
      }
        &::before {
        background-color: $lightgray;
        content: "";
        display: inline-block;
        height: 1px;
        position: relative;
        vertical-align: middle;
        width: 30px;
        margin-right: 5px;
      }
    }
  }