   @media (max-width: 768px) {
     // header css
     .header {
       z-index: 999;
     }
    //  content
    .content .content-white .content-white-child {
        margin-top:120px;
      }
     // sidebar 
    .container{
      width: 100%;
      padding:0;
    }
    .sidebar-drive {
      width: 100%;
    }
    .content {
      width: 100%;
      margin-left: 0; 
    }
    .sidebar-drive {
      display: none;
      z-index: 999;
    }
    .sidebar-drive .logo-drive {
      height: 111px;
    }
    // login page css
    .form-login-drive {
      width: 80%;
    }
    .login {
      background-size: cover;
      height: 145vh;
    }
    .sign-in-free {
      font-size: 14px;
    }
    //email sent css
    .icon-next {
      right: 6px;
    }
    
    // driver header css 
    .title-drive-content p {
      width: 100%;
      position: relative;
      left: 0;
    }
    .content-drive-active .tab-drive{
      margin-top: 190px;
    }
    .content-drive-active .tab-drive {
      width: 100%;
    }
    .title-drive-content {
      padding-left: 5px;
    }
    .title-drive-content h2 {
      font-size: 20px;
    }
    // driver details css
    .content-drive-details .drive-details .drive-details-right h1 {
      padding-left: 15px;
    }
    .status-duty .status.onduty {
      width:55%;
    }
    .content-drive-details .drive-details .drive-details-left .status-bottom .btn-submit-adddress {
      padding:12px 10px;
    }
    .user-driver-details .content-user {
      width: 90%;
    }
    .content-drive-details .drive-details .drive-details-left {
      width: 100%;
    }
    .content-drive-details .drive-details .drive-details-right {
      width: 100%;
      margin-left: 0;
    }
    .content-drive-details .drive-details {
      padding-left: 0;
    }
    .status-duty .status.slepper {
      width: 60%;
    }
    .content-chart .content-details-right .on-off a {
      width: 50%;
    }
    .content-drive-details .drive-details {
      margin-top: 120px;
    }
}

@media (max-width: 1366px) {
  .header .header-drive ul {
    display: block;
  }
  .container {
    padding:0;
    width: 100%;
  }
  // login page css
  .form-login-drive h1 {
    font-size: 20px;
  }
}
@media screen and (max-width: 992px) and (min-width: 769px) {
    // login page css
  .form-login-drive {
    width: 50%;
  }
  .login {
    background-size: cover;
    height: 130vh;
  }
  // driver page css
}
@media screen and (max-width: 1200px) and (min-width: 993px) {
    // login page css
   .form-login-drive {
      width: 50%;
    }
    .login {
      background-size: cover;
      height: 123vh;
    }
    // driver page css
}
