.content-drive-details {
  .drive-details {
    padding:20px 0 0 35px;
    background: $bg-drive;
    display: block;
    overflow: hidden;
    padding-bottom: 50px;
    .drive-details-left {
      width:30%;
      display: block;
      overflow: hidden;
      float: left;
      padding-bottom: 30px;
      background: $white;
      border: 1px solid #f8f8fa;
      .avatar-details {
        position: relative;
        padding-top: 15px;
        img {
          width: 80px;
          height: 80px;
          border-radius: 50%;
        }
        h5 {
          color: $black;
          font-size: 20px;
          font-weight: 600;
        }
        a {
          border-radius: 50%;
          position: absolute;
          padding-top:3px;
          right: 10px;
          background: #e8e8e8;
          top:15px;
          width: 28px;
          height: 28px;
          color: #7e7e7a;
        }
      }
      .content-detail-left {
        text-align: left;
        padding-left: 15px;
        border-bottom: 1px solid #f8f8fa;
        padding-bottom: 20px;
        margin-top:30px;
        p{
          font-size: 11px;
          color: $gray;
          font-weight: 600;
          margin:0;
        }
        .state {
          margin-top:20px;
          color: #dfdfdf;
          font-weight: 600;
          font-size: 11px;
        }
        h6 {
          font-weight: bold;
          font-size: 14px;
          margin:0;
        }
      }
      .details-email {
        text-align: left;
        padding-left: 15px;
        border-bottom: 1px solid #f8f8fa;
        padding-bottom: 20px;
        margin-top:30px;
        .state {
          margin-top:20px;
          color: #dfdfdf;
          font-weight: 600;
          font-size: 11px;
        }
        p{
          font-size: $heading;
          color: $gray;
          font-weight: 600;
          margin:0;
        }
        .state {
          margin-top:20px;
        }
        h6 {
          font-weight: bold;
          font-size: 14px;
          margin:0;
          color: $offduty;
        }
      }
      .status-bottom {
        padding: 0 15px;
        h4 {
            float: left;
            font-weight: bold;
            font-size: 15px;
          }
          .number {
            float: right;
            margin-top: 10px;
            color: $red;
          }
          .btn-submit-adddress {
            text-decoration: none;
            color: $black;
            font-size: 12px;
            font-weight: bold;
            display: block;
            overflow: hidden;
            width: 93%;
            background: $yellow;
            border-radius: 15px;
            float: right;
            margin-top: 15px;
            padding:3px 10px;
          }
        .btn-startut {
          display: block;
          overflow: hidden;
          h3 {
            float: left;
            color: #000;
            font-weight: bold;
            font-size: 18px;
          }
          .status-slider {
            float: right;
            margin-top: 18px;
          } 
          p {
            color: #adadab;
            font-size: 13px;
            font-weight: 600;
          }
        }
        p {
          display: inline-block;
          text-align: left;
          color: $gray;
        }
      }
    }
    .drive-details-right {
      width: 65%;
      float: left;
      margin-left: 30px;
      
      h1 {
        color: $black;
        font-weight: bold;
        text-align: left;
        margin-top: 0;
        font-size: 21px;
        span {
          color: $black;
          font-weight: 400;
        }
      }
    }
  }
}

.content-chart {
  display: block;
  padding:10px 20px;
  background: $white;
  overflow: hidden;
  .content-detail-left {
    width: 50%;
    float: left;
    p {
      text-align: left;
    }
    .heading-details {
      color: $gray91;
      font-weight: 600;
      font-size: 11px;
      margin-bottom: 0;
    }
    .title-location-detail {
      margin-top: 35px;
      .location {
        color: $lightgray;
        margin-bottom: 0;
      }
      .title-location {
        color: $eld;
        font-weight: bold;
        margin-bottom: 0;
        font-size: 15px;
        span {
          font-weight: 500;
        }
      }
      .time {
        color: $eld;
        font-weight: 700;
        font-size: 11px;
      }
    }
  }
  .content-details-right {
    width: 50%;
    float: left;
    .on-off {
      text-align: left;
      a {
        width: 20%;
        text-align: center;
        display: block;
        padding-top:3px;
        font-weight: bold;
        color: $black;
        background: $silver;
        text-decoration: none;
      }
    }
    p {
      text-align: left;
    }
    .heading-details {
      color: $gray91;
      font-weight: 600;
      font-size: 11px;
      margin-bottom: 0;
    }
    .title-location-detail {
      margin-top: 40px;
      .location {
        color: $lightgray;
        margin-bottom: 0;
      }
      .title-location {
        color: $eld;
        font-weight: bold;
        margin-bottom: 0;
        span {
          font-weight: 400;
        }
      }
      .time {
        color: $eld;
        font-weight: 600;
        font-size: 15px;
      }
    }
  }
}

.submit-btn-detaile {
    text-align: center;
    display: inline-block;
    width: 100%;
    margin-top:20px;
    font-weight: 600;
    a {
      color: $black;
      text-decoration: none;
      background: $yellow;
      padding:10px 15px;
      border-radius: 5px;
      font-weight: 700;
      font-size: 13px;
    }
  }
  .user-driver-details {
    text-align: center;
    margin-top: 30px;
    .icon-user-detaile {
      width: 75px;
      height: 75px;
      border-radius: 50%;
      padding-top: 14px;
      text-align: center;
      margin: auto;
      background: #8a92ac;
      font-size: 35px;
    }
    .content-user {
      border: 1px solid #ccc;
      background: $white;
      width: 60%;
      padding:20px 30px;
      text-align: left;
      margin: 30px auto;
      p {
        color: $gray81;
        font-size: 15px;
        font-weight: 400;
        span {
          font-weight: bold;
        }
      }
    }
  }


.status-duty {
  text-align: left;
  .status {
      border-radius: 3px;
      color: $white;
      width: 24%;
      padding: 4px 0;
      text-align: center !important;
      &.onduty{
          background: $onduty;
          font-weight: 600;
          font-size: 11px;
      }
      &.driving{
          background: $driving;
          font-weight: 600;
          font-size: 11px;
      }
      &.offduty{
          background: $offduty;
          font-weight: 600;
          font-size: 11px;
      }
      &.slepper{
          background: $sleeper;
          font-weight: 600;
          font-size: 11px;
      }

  }
  .status-duty-icon {
      color: $eld;
      font-weight: bold;
  }
}


.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px;
}



.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: $driving;
}

input:checked + .slider:before {
  -webkit-transform: translateX(13px);
  -ms-transform: translateX(13px);
  transform: translateX(13px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}